
import { minify } from 'html-minifier-terser';

import { Octokit } from '@octokit/rest';

import { Base64 } from 'js-base64';

import got from 'got';


/**
 * @param { string } user 
 * @returns object
 */
let fetchDevPosts = async( user = '' ) => await got(
   'https://dev.to/api/articles?username=' + user, {
      encoding: 'utf8', headers: { 'Content-Type': 'application/json' }
   }
).json();


/**
 * @param { string } string 
 * @param { object } config 
 * @returns string
 */
let minHTML = async( string = '', config = {

   collapseWhitespace: true,
   removeAttributeQuotes: true,
   removeComments: true,
   minifyCSS: true,
   minifyJS: true

}) => await minify( string, config );


/**
 * @param { string } auth 
 * @param { string } user 
 * @param { string } email 
 * @param { string } repo 
 * @param { string } path 
 * @param { string } content 
 * @param { string } message 
 * @returns 
 */
let octokitUpdateFile = async(

   auth ='', user ='', email ='', repo ='', path ='', content ='', message ='',

)=> await ( async octokit => auth && await octokit.repos.createOrUpdateFileContents({

    owner:user, repo:repo,  path:path, message:message, content:Base64.encode(content),
   committer: { user, email }, author: { user, email }

}))(  new Octokit({ auth:auth })  );


export { minHTML, fetchDevPosts, octokitUpdateFile };
